<template>
  <transition name="fade">
    <div id="model" v-show="modelState" @keyup.esc="openModel(0)">
      <div class="model-wraper">
        <a href="#" class="btn-close" @click.prevent="openModel(0)">
          <i class="material-icons">close</i>
        </a>
        <nav class="nav">
          <a
            href="#"
            @click.prevent="openModel(1)"
            class="link"
            :class="[modelState === 1 ?'link-active' : '']"
          >
            <span class="link-icon">
              <i class="material-icons">list</i>
            </span>
            to-do list
          </a>
          <a
            href="#"
            @click.prevent="openModel(2)"
            class="link"
            :class="[modelState === 2 ? 'link-active' : '']"
          >
            <span class="link-icon">
              <i class="material-icons">insert_chart</i>
            </span>
            analytics
          </a>
          <a
            href="#"
            @click.prevent="openModel(3)"
            class="link"
            :class="[modelState === 3 ? 'link-active' : '']"
          >
            <span class="link-icon">
              <i class="material-icons">library_music</i>
            </span>
            ringtones
          </a>
        </nav>
        <div class="content">
          <Todo v-show="modelState===1"></Todo>
          <Ringtones v-show="modelState===3"></Ringtones>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import Todo from "@/components/Todo";
import Ringtones from "@/components/Ringtones";
export default {
  components: {
    Todo,
    Ringtones
  },
  computed: {
    ...mapState(["modelState"])
  },
  methods: {
    ...mapMutations(["openModel"])
  }
};
</script>
<style lang="scss" scoped>
#model {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #003164;
  z-index: 200;
}
.model-wraper {
  width: 100%;
  min-width: 1110px;
  margin: 0 auto;
  height: 100vh;
  min-height: 400px;
  display: flex;
  justify-content: flex-start;
  position: relative;
  padding: 50px 80px;
}
.nav {
  margin-right: 250px;
}
.content {
  width: 450px;
}
.btn-close {
  position: absolute;
  top: 50px;
  right: 80px;
  font-size: 48px;
  color: #fff;
  cursor: pointer;
}
.link {
  display: block;
  text-transform: uppercase;
  font-size: 36px;
  color: rgba(255, 255, 255, 0.2);
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-weight: bold;
  &:hover,
  &.link-active {
    color: #ff4384;
  }
}
.link-icon {
  margin-right: 8px;
}
</style>
