import Vue from "vue";
import Vuex from "vuex";
import moment from "moment";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // todo-list
    todoList: [
      // 
      {
        id: moment().format("DDMMYYYYhmmss"),
        title: "the First thing to do today",
        isDone: false,
        tomatos: 5
      },
      {
        id: 1,
        title: "the Second thing to do today",
        isDone: false,
        tomatos: 2
      },
      {
        id: 2,
        title: "the Third thing to do today",
        isDone: false,
        tomatos: 3
      }
    ],
    todoId: moment().format("DDMMYYYYhmmss"), // todoid
    timer: null, // Interval
    time: 3, // 
    breakTime: 3, //
    workTime: 3, //
    timerState: "pause", //  : 'play' 'pause'
    workState: "work", //  : 'work' 'break'
    modelState: 0, //  CLOSE : 0  TODO : 1 ANALYTIC : 2 SRINGTONES : 3
    // 
    alarmList: [
      {
        id: 1,
        title: "beep",
        src: require("./assets/audio/beep.mp3")
      },
      {
        id: 2,
        title: "bell",
        src: require("./assets/audio/bell.mp3")
      },
      {
        id: 3,
        title: "bugle",
        src: require("./assets/audio/bugle.mp3")
      },
      {
        id: 4,
        title: "digital",
        src: require("./assets/audio/digital.mp3")
      },
      {
        id: 5,
        title: "drop",
        src: require("./assets/audio/drop.mp3")
      },
      {
        id: 6,
        title: "horn",
        src: require("./assets/audio/horn.mp3")
      },
      {
        id: 7,
        title: "warning",
        src: require("./assets/audio/warning.mp3")
      },
      {
        id: 8,
        title: "alarm",
        src: require("./assets/audio/alarm.mp3")
      }
    ],
    alarmWorkId: 1, // id
    alarmBreakId: 2, // id
    alarm: new Audio(), //
    alert: false
  },
  mutations: {
    //  model CLOSE : 0  TODO : 1 ANALYTIC : 2 SRINGTONES : 3
    openModel(state, id) {
      state.modelState = id;
    },
    //  todo
    addTodo(state, title) {
      //  title ''
      if (title) {
        var now = moment().format("DDMMYYYYhmmss"); // ID = 
        state.todoList = [
          ...state.todoList,
          {
            id: now,
            title: title,
            isDone: false,
            tomatos: 0
          }
        ];
        // todoId=='' todo.id = todoId
        if (state.todoId === "") {
          state.todoId = state.todoList.find(item => {
            return !item.isDone;
          }).id;
        }
      }
    },
    //  todo
    changeTodo(state, id) {
      state.todoId = id;
      // 
      clearInterval(state.timer);
      //  : workTime
      state.time = state.workTime;
      // : pause
      state.timerState = "pause";
      // : work
      state.workState = "work";
    },
    //  todo 
    compeleteTodo(state, id) {
      let tomato = state.todoList.find(item => {
        return item.id == id;
      });
      tomato.isDone = true;
      // 
      if (state.todoId === id) {
        // 
        clearInterval(state.timer);
        //  : workTime
        state.time = state.workTime;
        // : pause
        state.timerState = "pause";
        // : work
        state.workState = "work";

        //  todo
        var newId = state.todoList.find(item => {
          return item.isDone === false;
        });
        if (newId) {
          state.todoId = newId.id;
        } else {
          // todo , todoId = ''
          state.todoId = "";
        }
      }
    },
    // 
    tickTime(state, timer) {
      //  : work
      if (state.workState === "work") {
        //  -1
        state.time = state.time - 1;
        if (state.time < 0) {
          // 
          clearInterval(state.timer);
          //  : break
          state.workState = "break";
          //  : breakTime
          state.time = state.breakTime;
          // todo.tomatos + 1
          let tomato = state.todoList.find(item => item.id === state.todoId);
          tomato.tomatos = tomato.tomatos + 1;
          //  : pause
          state.timerState = "pause";

          // 
          let src = "";
          if (state.alarmWorkId) {
            src = state.alarmList.find(item => {
              return item.id === state.alarmWorkId;
            }).src;
          }
          state.alarm.src = src;
          state.alarm.loop = true;
          state.alert = true;
          state.alarm.play();
        }
      } // : break
      else {
        //  -1
        state.time = state.time - 1;
        if (state.time < 0) {
          // 
          clearInterval(state.timer);
          //  : work
          state.workState = "work";
          //  : workTime
          state.time = state.workTime;
          //  : pause
          state.timerState = "pause";

          // 
          let src = "";
          if (state.alarmBreakId) {
            src = state.alarmList.find(item => {
              return item.id === state.alarmBreakId;
            }).src;
          }
          state.alarm.src = src;
          state.alarm.loop = true;
          state.alert = true;
          state.alarm.play();
        }
      }
    },
    // 
    resetTime(state) {
      //  :work
      if (state.workState === "work") {
        // 
        clearInterval(state.timer);
        //  : workTime
        state.time = state.workTime;
        //  : pause
        state.timerState = "pause";
      } //  :break
      else {
        // 
        clearInterval(state.timer);
        //  : breakTime
        state.time = state.breakTime;
        //  : pause
        state.timerState = "pause";
      }
    },
    //  workid
    setWorkId(state, id) {
      state.alarmWorkId = id;
    },
    //  breakid
    setBreakId(state, id) {
      state.alarmBreakId = id;
    },
    closeAlarm(state) {
      state.alert = false;
      state.alarm.pause();
    }
  },
  actions: {
    // 
    startTime(context) {
      if (context.state.timerState === "pause") {
        context.state.timer = setInterval(() => {
          context.commit("tickTime", this);
        }, 1000);
        context.state.timerState = "play";
      } else {
        clearInterval(context.state.timer);
        context.state.timerState = "pause";
      }
    }
  },
  getters: {
    // T todo-list
    todos(state) {
      return state.todoList.filter(item => !item.isDone);
    },
    //   todo-list
    done(state) {
      return state.todoList.filter(item => item.isDone);
    },
    //  todo
    doing(state) {
      return state.todoList.find(item => {
        return item.id === state.todoId;
      });
    },
    //   todo-list
    homelist(state, getters) {
      return getters.todos
        .filter((item, idx, arr) => {
          return arr.length - idx <= 3 && item.id !== state.todoId;
        })
        .reverse();
    },
    //  
    min(state) {
      return Math.floor(state.time / 60);
    },
    //  
    sec(state) {
      if (Math.ceil(state.time % 60).toString().length < 2) {
        return "0" + Math.ceil(state.time % 60);
      } else {
        return Math.ceil(state.time % 60);
      }
    },
    // 
    progress(state) {
      if (state.workState === "work") {
        return state.time / state.workTime;
      } else {
        return state.time / state.breakTime;
      }
    },
    workAlarm(state) {
      if (state.alarmWorkId) {
        return state.alarmList.find(item => {
          return item.id === state.alarmWorkId;
        }).src;
      } else return "";
    },
    breakAlarm(state) {
      if (state.alarmBreakId) {
        return state.alarmList.find(item => {
          return item.id === state.alarmBreakId;
        }).src;
      } else return "";
    }
  }
});
